import style from "../../AdmissionManager/OngoingAdmission/OngoingAdmission.module.css";
import { assestsUserAdmissionUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";

const DepartmentOngoingApplicationCard = ({
  application,
  isAuthorization,
  urlOffSet,
}) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();
  const getQuery = useLocation();

  const onNavigate = (e, value) => {
    if (e) e.stopPropagation();
    let url = "";
    if (value)
      url =
        urlOffSet === "NORMAL"
          ? `/q/${params.username}/member/department/${params.deptname}?a=ongoing-detail&n=${value}`
          : `/q/${params.username}/member/staff/academic/department/${params.name}?type=ongoing-detail&n=${value}`;
    else
      url =
        urlOffSet === "NORMAL"
          ? `/q/${params.username}/member/department/${params.deptname}?a=ongoing-detail`
          : `/q/${params.username}/member/staff/academic/department/${params.name}?type=ongoing-detail`;

    navigate(url, {
      state: {
        ...getQuery.state,
        appId: application?._id,
        applicationName: application?.applicationName,
        isAuthorization: isAuthorization,
        applicationData: {
          application: application?.receievedCount,
          document: application?.selectCount,
          fees: application?.fee_collect_count,
          confirmed: application?.confirmCount,
          review: application?.review_count,
          allottment: application?.allotCount,
          cancelled: application?.cancelCount,
        },
      },
    });
  };

  // console.info("application", getQuery.state?.subRole);
  return (
    <>
      <div
        className={style.ongoing_card}
        onClick={onNavigate}
        style={{
          position: "relative",
        }}
      >
        <div className={style.ongoing_card_inner}>
          <img
            alt="Ongoing App"
            src={`${assestsUserAdmissionUrl}/application.svg`}
          />
          <div className={style.ongoing_card_text}>
            <h6>{application?.applicationName ?? ""}</h6>
            <p>{application?.applicationDepartment?.dName ?? ""}</p>
          </div>
        </div>
        <div className={style.ongoing_card_count_date}>
          <div className={style.ongoing_card_count_container}>
            <div
              className={style.ongoing_card_count_container_inner}
              onClick={() => onNavigate("")}
            >
              <p title="Application">{t("application")}</p>
              <h6 title="Application">{application?.receievedCount ?? 0}</h6>
            </div>

            <div
              className={style.ongoing_card_count_container_inner}
              onClick={(e) => onNavigate(e, "document")}
            >
              <p title="Docs Collect">{t("docs")}</p>
              <h6 title="Docs Collect">{application?.selectCount ?? 0}</h6>
            </div>

            <div
              className={style.ongoing_card_count_container_inner}
              onClick={(e) => onNavigate(e, "fees")}
            >
              <p title="Fees Collection">{t("fees_")}</p>
              <h6 title="Fees Collection">
                {application?.fee_collect_count ?? 0}
              </h6>
            </div>

            <div
              className={style.ongoing_card_count_container_inner}
              onClick={(e) => onNavigate(e, "confirmed")}
            >
              <p title="Confirmed">{t("confirm")}</p>
              <h6 title="Confirmed">{application?.confirmCount ?? 0}</h6>
            </div>

            <div
              className={style.ongoing_card_count_container_inner}
              onClick={(e) => onNavigate(e, "review")}
            >
              <p title="Review">{t("review")}</p>
              <h6 title="Review">{application?.review_count ?? 0}</h6>
            </div>

            <div
              className={style.ongoing_card_count_container_inner}
              onClick={(e) => onNavigate(e, "allottment")}
            >
              <p title="Allottment">{t("allotted")}</p>
              <h6 title="Allottment">{application?.allotCount ?? 0}</h6>
            </div>

            <div
              className={style.ongoing_card_count_container_inner}
              onClick={(e) => onNavigate(e, "cancelled")}
            >
              <p title="Cancelled">{t("cancel")}</p>
              <h6 title="Cancelled">{application?.cancelCount ?? 0}</h6>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default DepartmentOngoingApplicationCard;

const authorized = {
  isCompleted: true,
};
