import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../PlateformUser/PlateformUser.module.css";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useEffect, useState } from "react";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import DepartmentImportApplicationSelectCard from "./DepartmentImportApplicationSelectCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useDepartmentOngoingApplicationList } from "../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
const DepartmentImportApplicationSelect = ({
  onClose,
  onSelect,
  departmentId,
  selectedId,
  onDone,
  isMultiSelect,
  buttonText,
}) => {
  const { t } = useTranslation();
  const [searchCopy, setSearchCopy] = useState("");
  const [applicationList, setApplicationList] = useState([]);

  const {
    departmentOngoingApplicationList,
    departmentOngoingApplicationListLoading,
  } = useDepartmentOngoingApplicationList({
    data: {
      did: departmentId,
    },
    skip: !departmentId,
  });
  useEffect(() => {
    if (departmentOngoingApplicationList?.list?.length) {
      setApplicationList(departmentOngoingApplicationList?.list);
    }
  }, [departmentOngoingApplicationList?.list]);

  const onSearchEvent = (e) => {
    if (
      !departmentOngoingApplicationListLoading &&
      departmentOngoingApplicationList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      let ft = getApplicationFilter(
        departmentOngoingApplicationList?.list,
        searchCopy
      );
      setApplicationList(ft);
    }
  };

  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("selected_ongoing_applications")}</h6>
              </div>

              <div className={style.modal_container_btn_container}>
                {isMultiSelect && (
                  <CreateButton
                    label={t(buttonText ?? "select")}
                    count={selectedId?.length}
                    onAction={onDone}
                    customStyle={{
                      margin: "0",
                      padding: "0.3rem 0.5rem",
                    }}
                  />
                )}
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                  className={style.close_icon}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <div
              className={style.assign_search_container}
              style={{
                marginBottom: "1rem",
              }}
            >
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>
            {applicationList?.map((application) => (
              <DepartmentImportApplicationSelectCard
                application={application}
                onSelect={onSelect}
                isList={isMultiSelect}
                selectedId={selectedId}
                key={application?._id}
              />
            ))}

            {departmentOngoingApplicationListLoading ? (
              searchCopy ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : searchCopy ? (
              applicationList?.length === 0 && (
                <EmptyMessage
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                  title="No ongoing application found related this search."
                />
              )
            ) : (
              departmentOngoingApplicationList?.list?.length === 0 && (
                <EmptyMessage
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                  title="No application added."
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default DepartmentImportApplicationSelect;

const getApplicationFilter = (list, args) => {
  if (list?.length > 0) {
    return list?.filter((val) => {
      if (val?.applicationName?.toLowerCase()?.includes(args?.toLowerCase())) {
        return val;
      } else {
        return null;
      }
    });
  } else {
    return [];
  }
};
