import { useCallback, useEffect, useState } from "react";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  assestsNavbarUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import DepartmentOngoingApplicationCard from "./DepartmentOngoingApplicationCard";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  useDepartmentImportOngoingApplication,
  useDepartmentOngoingApplicationList,
} from "../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
import TransferSelectOngoingAppllication from "../../AdmissionManager/OneOngoingAdmission/TransferApplication/TransferSelectOngoingAppllication";
import DepartmentImportApplicationSelect from "./DepartmentImportApplicationSelect";

const DepartmentOngoingApplication = ({
  departmentId,
  admissionId,
  urlOffSet,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchCopy, setSearchCopy] = useState("");
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [applicationList, setApplicationList] = useState([]);
  const [state, setState] = useState({
    add: [],
    remove: [],
  });

  const [departmentImportOngoingApplication] =
    useDepartmentImportOngoingApplication();
  const {
    departmentOngoingApplicationList,
    departmentOngoingApplicationListLoading,
    departmentOngoingApplicationListRefetch,
  } = useDepartmentOngoingApplicationList({
    data: {
      did: departmentId,
    },
    skip: !departmentId,
  });

  useEffect(() => {
    if (departmentOngoingApplicationList?.list?.length) {
      setApplicationList(departmentOngoingApplicationList?.list);
    }
  }, [departmentOngoingApplicationList?.list]);

  const onSearchEvent = (e) => {
    if (
      !departmentOngoingApplicationListLoading &&
      departmentOngoingApplicationList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      let ft = getApplicationFilter(
        departmentOngoingApplicationList?.list,
        searchCopy
      );
      setApplicationList(ft);
    }
  };

  const onNavigate = useCallback(() => {
    let url =
      urlOffSet === "NORMAL"
        ? `/q/${params.username}/member/department/${params.deptname}`
        : `/q/${params.username}/member/staff/academic/department/${params.name}`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onClose = () => {
    setStep("");
  };
  const onAddSelect = (val) => {
    if (state.add?.includes(val?._id)) {
      setState((prev) => ({
        ...prev,
        add: prev?.add?.filter((dt) => dt !== val?._id),
      }));
    } else {
      setState((prev) => ({
        ...prev,
        add: [...prev.add, val?._id],
      }));
    }
  };
  const onImportApplication = () => {
    if (departmentId && state.add?.length > 0) {
      setDisabled((pre) => !pre);
      departmentImportOngoingApplication({
        did: departmentId,
        bodyParameter: {
          flow: "ADD",
          apps: state.add,
        },
      })
        .then(() => {
          departmentOngoingApplicationListRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onRemoveSelect = (val) => {
    if (state.add?.includes(val?._id)) {
      setState((prev) => ({
        ...prev,
        remove: prev?.remove?.filter((dt) => dt !== val?._id),
      }));
    } else {
      setState((prev) => ({
        ...prev,
        remove: [...prev.remove, val?._id],
      }));
    }
  };
  const onRemoveApplication = () => {
    if (departmentId && state.remove?.length > 0) {
      setDisabled((pre) => !pre);
      departmentImportOngoingApplication({
        did: departmentId,
        bodyParameter: {
          flow: "REMOVE",
          apps: state.remove,
        },
      })
        .then(() => {
          departmentOngoingApplicationListRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  // console.info("departmentOngoingApplicationList", state);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.5rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("ongoing_applications")}</h6>
            </div>

            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "220px",
                    }}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>
              <CreateButton
                label="add_application"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setStep(1)}
              />
              <CreateButton
                label="remove_application"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setStep(2)}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
            position: "relative",
          }}
        >
          {applicationList?.map((application) => (
            <DepartmentOngoingApplicationCard
              application={application}
              key={application?._id}
              urlOffSet={urlOffSet}
            />
          ))}

          {departmentOngoingApplicationListLoading ? (
            searchCopy ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : searchCopy ? (
            departmentOngoingApplicationList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No ongoing application found related this search."
              />
            )
          ) : (
            departmentOngoingApplicationList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No application added."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {step === 1 ? (
        <TransferSelectOngoingAppllication
          onClose={onClose}
          aid={admissionId}
          buttonText="add_application"
          isList
          selectedId={state.add}
          onSelect={onAddSelect}
          onDone={onImportApplication}
          isMultiSelect
        />
      ) : step === 2 ? (
        <DepartmentImportApplicationSelect
          onClose={onClose}
          departmentId={departmentId}
          buttonText="remove_application"
          isList={state.remove}
          onSelect={onRemoveSelect}
          onDone={onRemoveApplication}
          isMultiSelect
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default DepartmentOngoingApplication;

const getApplicationFilter = (list, args) => {
  if (list?.length > 0) {
    return list?.filter((val) => {
      if (val?.applicationName?.toLowerCase()?.includes(args?.toLowerCase())) {
        return val;
      } else {
        return null;
      }
    });
  } else {
    return [];
  }
};
